import React from "react";

const TodoInput = ({ handleAdd }) => {
  const [title, setTitle] = React.useState("");

  return (
    <div>
      <input
        type="text"
        placeholder="Add something"
        onChange={(e) => setTitle(e.target.value)}
      />

      <button onClick={() => handleAdd(title)}>ADD</button>
    </div>
  );
};

export { TodoInput };
