import React from "react";

const TodoItem = ({ title, status, id, handleToggle, handleDelete }) => {
  return (
    <div
      style={{
        display: "flex",
        alignItems: "center",
        justifyContent: "space-around"
      }}
    >
      <h1>{title}</h1>
      <h2>{status ? "Completed" : "not Completed"}</h2>
      <button onClick={() => handleToggle(id)}>update Todo</button>
      <button onClick={() => handleDelete(id)}>Delete Todo</button>
    </div>
  );
};

export { TodoItem };
